<template>
  <div class="studentManagement" v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="页面加载中...">
    <div class="studentMgt-header">
      <el-button type="primary"> <router-link to="editstudent"> 新建学生 </router-link>   </el-button>
      <el-button type="primary"> <router-link to="editstudent">批量导入学生</router-link> </el-button>
      <el-button type="primary" @click="manageClass">分班</el-button>
      <el-button type="primary">删除学生</el-button>
    </div>
    <div class="studentMgt-filter">
      <div class="grades">
        <el-radio-group v-model="gradeId" @change="getCurrentGradeId(gradeId)">
          <el-radio-button v-for="(item,index) in grades" :label="item.index" :key="index"> {{item.name}} </el-radio-button>
        </el-radio-group>
      </div>
      <div class="classes">
        <el-radio-group v-model="classId" @change="getCurrentClassId(classId)">
          <el-radio-button v-for="(item,index) in classes" :label="item.index" :key="index"> {{item.name}} </el-radio-button>
        </el-radio-group>
      </div>
    </div>
    <div class="studentMgt-body">
      <div class="studentMgt-body-title" >
        <p v-if=" !isHeadTeacher "> 该班级暂无班主任 </p>
        <template v-if="isHeadTeacher">
          <h4 class="item"> 班主任：{{headTeacher.name}}</h4>
          <h4 class="item">手机号码：{{headTeacher.phone}}</h4>
        </template>
        
      </div>
      <div class="studentMgt-body-table">
        <el-table
          :data="studentClassListsData"
          @row-click="getCurrentRowInfo"
          style="width: 100%">
          <el-table-column
            type="selection"
            width="30">
          </el-table-column>
          <el-table-column
            prop="name"
            label="姓名"
            width="100">
          </el-table-column>
          <el-table-column
            prop="sex"
            label="性别"
            width="80">
            <template scope="scope">
              <span v-if=" scope.row.sex == 1 ">
                男
              </span>
              <span v-if=" scope.row.sex == 2 ">
                女
              </span>
            </template>
          </el-table-column>
          <el-table-column
            prop="nation"
            label="民族">
          </el-table-column>
          
          <el-table-column
            prop="sid"
            label="学籍号" >
          </el-table-column>
          
          <el-table-column
            prop="schoolYear"
            label="入学年份">
          </el-table-column>
          <el-table-column
          
            label="状态">
            <template scope="scope">
              <el-dropdown trigger="click" @command="changeStudensState">
                    <span class="el-dropdown-link">
                      <b v-if="scope.row.state == 0">正常</b>
                      <b v-else-if="scope.row.state == 1"> 禁用</b>
                      <b v-else-if="scope.row.state == 2">休学</b>
                      <b v-else="scope.row.state == 3">离校</b>
          
                      <i class="el-icon-caret-bottom el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item command="0">正常</el-dropdown-item>
                      <el-dropdown-item command="1">禁用</el-dropdown-item>
                      <el-dropdown-item command="2">休学</el-dropdown-item>
                      <el-dropdown-item command="3">离校</el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
          
          
            </template>
          </el-table-column>
          <el-table-column
            label="操作"
            width="280"
            >
            <template scope="scope">
                <el-button
                  type="primary"
                  size="small"
                  >
                  <router-link :to="{ name: 'editstudent', params: {id: scope.row.id,type:'detail' , gradeId:gradeId, classId:classId  }}" >查看</router-link>
          
                </el-button>
                <el-button
                  type="primary"
                  size="small"
                  >
                  <router-link :to="{ name: 'editstudent', params: { id: scope.row.id, type:'edit', gradeId:gradeId, classId:classId }}">编辑</router-link>
                  </el-button>
                <el-button
                  type="primary"
                  size="small"
                  @click="studentRestPwd(scope.$index, scope.row)">重置密码</el-button>
                <el-button
                  type="danger"
                  size="small"
                  @click="studentDelete(scope.$index, scope.row)">删除</el-button>
              </template>
          </el-table-column>
          
        </el-table>
        <div class="pagination">
            <el-pagination
              :page-size="10"
              layout="prev, pager, next, jumper"
              :total="total"
              :current-page="currentPage"
              @current-change="paginationCurrentChange"
              >
            </el-pagination>
        </div>
      </div>
    </div>
    <el-dialog title="分班管理" v-model="dialogVisible"  :close-on-click-modal="false" size="small" >
      <div class="transfer-data">
        <div class="transfer-data-left">
          <div class="transfer-data-header">
            <h2 class="title">年级池</h2>

            <el-select v-model="gradeAllName" @change="gradesAllSelected(gradeAllName)"   placeholder="请选择">
              <el-option
                v-for=" (item,index) in grades"
                :label="item.name"
                :value="item.index"
                :key="index"
                :disabled="index ==0 "
                >
              </el-option>
            </el-select>

            <el-input placeholder="姓名模糊筛选" v-model="gradesSearchText" @change="gradesChange(gradesSearchText)">
              <el-button slot="append" icon="search" @click="gradesSearch(gradesSearchText)"></el-button>
            </el-input>
          </div>
          <div class="transfer-data-body">
            <el-table
              :data="studentGradeListsData"
              style="width: 100%"
              @selection-change="studentsSelectionChange"
              @select-all="addStudentsSelectionAll"
              empty-text="请通过新增学生或者批量导入学生的方式维护班级学生"
              >
              <!-- @current-change="currentChange" -->
              <el-table-column
                    type="selection"
                    width="30">
                  </el-table-column>
              <el-table-column
                prop="sid"
                label="学籍号"
                >
              </el-table-column>

              <el-table-column
                prop="name"
                label="姓名">
              </el-table-column>
              <el-table-column
                 prop="className"
                label="上学年年级班级"
                >
              </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                  :page-size="10"
                  layout="prev, pager, next, jumper"
                  :total="gradesTotal"
                  @current-change="gradesPaginationCurrentChange"
                  >
                </el-pagination>
            </div>
            
          </div>
        </div>
        <div class="transfer-data-operation">
        
          <el-button type="primary" icon="arrow-right" @click="addStudent" :disabled=" addStudensState ">添加</el-button>
          <el-button type="primary" icon="arrow-left"  @click="removeStudent"  :disabled=" removeStudensState ">移除</el-button>
          <!-- <el-button type="primary" icon="upload" @click="saveStudents" :disabled=" saveStudensState ">保存</el-button> -->
        
        
        </div>
        <div class="transfer-data-right">
        
          <div class="transfer-data-header">
            <h2 class="title">所选班级</h2>
            <el-select v-model="gradeName" @change="gradesSelected(gradeName)"   placeholder="请选择">
              <el-option
                v-for=" (item,index) in grades"
                :label="item.name"
                :value="item.index"
                :key="index"
                :disabled="index ==0 "
                >
              </el-option>
            </el-select>
        
            <el-select v-model="className"   placeholder="请选择">
              <el-option
                v-for=" (item,index) in classes"
                :label="item.name"
                :value="item.id"
                :key="index"
                >
              </el-option>
            </el-select>
        
            <el-input placeholder="姓名模糊筛选" v-model="classesSearchText" @change="classesChange">
              <el-button slot="append" icon="search" @click="classesSearch(classesSearchText)"></el-button>
            </el-input>
          </div>
          <div class="transfer-data-body">
            <el-table
              :data="editStudentsListsData"
              style="width: 100%"

              @selection-change="removeStudentsSelectionChange"
              @select-all="removeStudentsSelectionAll"
              >
              <el-table-column
                type="selection"
                width="30">
              
              </el-table-column>
              <el-table-column
                prop="sid"
                label="学籍号"
                >
              </el-table-column>
        
              <el-table-column
                prop="name"
                label="姓名"
                >
              </el-table-column>
              <!-- <el-table-column
                prop="className"
                label="历史班级"
                width="80">
              </el-table-column> -->
              
            </el-table>
          </div>
        
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="info" @click="dialogVisible = false">取 消</el-button>
        <el-button type="success" @click="saveStudents" :disabled=" saveStudensState " >添 加</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {

  name: 'studentManagement',

  data () {
    return {
      // 全屏遮罩层
      fullscreenLoading: false,
      // 默认学校
      schoolId:'111',
      // 弹框
      dialogVisible:false,
      // 班主任
      headTeacher:{},
      // 当前班级是否班主任
      isHeadTeacher:true,
      gradeAllName:'',
      gradeName:'',
      className:'',

      // 年级
      grades:[],
      // 默认年级
      gradeId:'',
      // 当前行信息
      currentRowInfo:'',

      // 班级
      classes:[],
      // 默认班级
      classId:'',
      // 当前班级id
      currentClassId:'',
      // 当前年级学生列表数据
      studentGradeListsData:[],
      // 当前班级学生列表数据
      studentClassListsData:[],
      // 学生数量
      studentListslength:'',
      // 所要编辑的学生列表
      editStudentsListsData:[],

      gradesSearchText:'',
      classesSearchText:'',

      

      // 当前年级总页数
      total:0,
      // 当前页
      currentPage:1,
      // 年级池总页数
      gradesTotal:0,
      // 选择添加学生集合
      addMultipleSelection:[],
      // 选择移除学生集合
      removeMultipleSelection:[],
      // 添加学生按钮状态
      addStudensState:true,
      // 移除学生按钮状态
      removeStudensState:true,
      // 保存学生按钮状态
      saveStudensState:true,
    };

  },
  mounted(){
    this.getGrade( this.gradeId )
    this.getSutdentGradeLists(this.gradeId)


  },
  methods:{
    // 年级列表
    getGrade(){
      let params={
        schoolId:this.schoolId
      };
      this.$ajax('/headteacher/findGradeByCurrentUser',params)

      .then(res => {
          // if (res.code == 200) {
            this.grades=res.data;

            this.gradeId = this.grades[0].index;

            this.gradeName = this.grades[1].name
            this.gradeAllName = this.grades[1].name
          // }
          



      }).catch(() => {

      });
    },
    // 获取当前年级
    getCurrentGradeId(gradeId){

      this.getClass(gradeId);
      this.getSutdentGradeLists(gradeId)

      this.getEditStudentsLists()
      

    },

    
    // 班级列表
    getClass(gradeId){
      let params={
        schoolId:this.schoolId,
        gradeId: gradeId
      };

      this.$ajax('/findClassByGradeIdAndSchoolId',params)

      .then(res => {
          this.classes=res.data;

          this.classId= this.classes[0].index;
          this.className= this.classes[0].name
          // this.classId= 152;

          this.getHeadTeacher(this.classId)

          // this.getSutdentClassLists(this.classId)

      }).catch(() => {

      });
    },

    // 获取当前班级
    getCurrentClassId(classId){

      this.currentPage=1;

      this.currentClassId=classId;

      this.getSutdentClassLists(this.currentClassId,1)

      this.getHeadTeacher(this.currentClassId)

    },

    // 获取当前班级学生列表
    getSutdentClassLists(currentClassId,currentPage){
      this.fullscreenLoading=true;
      let params={
          schoolId: this.schoolId,
          classId: currentClassId,
          // classId: 152,
          pageSize:10,
          pageNumber:currentPage
        };
        this.$ajax('/findStudentByClassIdAndSchoolId',params)

        .then(res => {
            if(res.code ==200){


              this.studentClassListsData=res.data.resultList;


              this.total = res.data.total;

              this.fullscreenLoading=false;

            }else{

              this.studentClassListsData=[];

              this.total = 0;

              this.fullscreenLoading=false;

            }
            

        }).catch(() => {

        });

    },
    // 获取点击当前行信息
    getCurrentRowInfo(row, column, cell, event){
      // console.log(row)
      this.currentRowInfo = row;
    },
    // 设置学生状态
    changeStudensState(command){

        // console.log(this.currentRowInfo)
        // console.log(command)
        let params={
          id: this.currentRowInfo.id,
          state: command
        };

        this.$ajax('/changeStudentState',params).then((res) => {

          if(res.code == 200){

            this.$message({
              message: '状态修改成功',
              type: 'success'
            });

            this.getSutdentClassLists(this.currentClassId,this.currentPage)
          }
      }).catch(() => {

      });
    },
    // 获取当前班级班主任
    getHeadTeacher(classId){
      let params={
          classId: classId,
          // classId: 21053,
        };
        this.$ajax('/findHeadTeacherByClassId',params)

        .then(res => {
            if(res.message =='success'){
              
              this.headTeacher=res.data;

              this.isHeadTeacher=true;

            }else{

              this.isHeadTeacher=false;

            }
            
        }).catch(() => {

        });
    },
    // 重置密码
    studentRestPwd(index,row){
      this.$confirm('是否要重置密码为学籍号后六位?', '提示', {
        type: 'warning',
        confirmButtonText: '确定',
        cancelButtonText: '取消',
      }).then(() => {

        let params = {
          id: row.id
        };

        this.$ajax('/password/reset',params).then((res) => {
          if(res.code == 200){
            this.$message({
              message: '删除成功',
              type: 'success'
            });

            this.getSutdentClassLists(this.currentClassId,this.currentPage)

          }

        });
      }).catch(() => {

      });
    },
    // 删除单个学生
    studentDelete(index,row){
      this.$confirm('确定要删除选中学生吗?', '提示', {
        type: 'warning',
        confirmButtonText: '确定',
        cancelButtonText: '取消',
      }).then(() => {

        let params = {
          id: row.id
        };

        this.$ajax('/teacher/delOneStudent',params).then((res) => {
          if(res.code == 200){
            this.$message({
              message: '删除成功',
              type: 'success'
            });

            this.getSutdentClassLists(this.currentClassId,this.currentPage)

          }

        });
      }).catch(() => {

      });
    },
    // 批量删除
    
    // 管理分班弹窗
    manageClass(){
      this.dialogVisible=true;
    },
    // 班级列表分页
    paginationCurrentChange(val){

      this.fullscreenLoading=true;

      this.currentPage = val;

      let params={
          schoolId: this.schoolId,
          classId: this.currentClassId,
          // classId: 152,
          pageSize:10,
          pageNumber:this.currentPage
        };
        this.$ajax('/findStudentByClassIdAndSchoolId',params)

        .then(res => {

            this.studentClassListsData=res.data.resultList;

            this.fullscreenLoading=false;

        }).catch(() => {

        });
    },

    // 年级池列表分页
    gradesPaginationCurrentChange(val){
      // console.log(val)
      let params={
          schoolId: 1921,
          gradeCode: 9,
          pageSize:10,
          currentPage:val
        };
        this.$ajax('/headteacher/findGradePoolByConditionAndPage',params)

        .then(res => {

            this.studentGradeListsData=res.data.rows;

        }).catch(() => {

        });

    },
    // 获取年级池中当前 年级
    gradesAllSelected(id){
      // console.log(id)
    },
    // 获取所要编辑年级
    gradesSelected(id){
      // this.gradeAllName=this.gradeName

      // console.log(id)
    },

    // 获取当前年级学生列表
    getSutdentGradeLists(gradeId){

      let params={
          schoolId: 1921,
          gradeCode: 9,
          pageSize:10,
          currentPage:1
        };
        this.$ajax('/headteacher/findGradePoolByConditionAndPage',params)

        .then(res => {
            this.studentGradeListsData=res.data.rows;
            this.gradesTotal=res.data.totalCount;
            // this.gradeName = this.grades[0].name

        }).catch(() => {

        });

    },
    // 年级池搜索
    gradesSearch(val){
      if(val !=''){
        let params={
            schoolId: 1921,
            gradeCode: 9,
            pageSize:10,
            currentPage:1,
            keyWord: val
          };
          this.$ajax('/headteacher/findGradePoolByConditionAndPage',params)

          .then(res => {

              this.studentGradeListsData=res.data.rows;
              this.gradesTotal=res.data.totalCount;


          }).catch(() => {

          });
      }else{
        return false
      }
      
    },
    // 判断年级池文本框为空是从新请求数据
    gradesChange(val){
      if(val.length == 0){
        this.getSutdentGradeLists(this.gradeId)
      }
    },
    // 获取要编辑年级的学生列表 班级id获取学生列表
    
    getEditStudentsLists(){
      let params={
          // classId: this.currentClassId,
          classId: 150,
        };
        this.$ajax('/headteacher/findStudentByClassId',params)

        .then(res => {

            this.editStudentsListsData=res.data;

        }).catch(() => {

        });
    },
    // 班级搜索
    classesSearch(val){
      if(val != ''){
        let params={
            // classId: this.currentClassId,
            classId: 150,
            keyWord: val
          };
          this.$ajax('/headteacher/findStudentByClassId',params)

          .then(res => {

              this.editStudentsListsData=res.data;

          }).catch(() => {

          });

      }else{
        return false
      }
      
    },
    // 判断当前班级文本框为空是从新请求数据
    classesChange(val){
      if(val.length == 0){
        this.getEditStudentsLists()
      }
    },
    // 选择添加学生改变时
    studentsSelectionChange(val){

      this.addMultipleSelection=val;

      if(this.addMultipleSelection.length > 0){
        this.addStudensState=false;
        this.saveStudensState=true;
      }
      if(this.addMultipleSelection == 0){
        this.addStudensState=true;
      }

      localStorage.setItem("addStudents", JSON.stringify(this.addMultipleSelection));


    },
    // 添加到缓存列表
    addStudent(){

      let addStudentsItem = JSON.parse(localStorage.getItem("addStudents"));

      if(addStudentsItem.length !=0 ){


        this.editStudentsListsData=_.concat(addStudentsItem,this.editStudentsListsData,)
        

        this.studentGradeListsData = _.differenceWith(this.studentGradeListsData , addStudentsItem , _.isEqual )

        this.addStudensState=true;

        this.saveStudensState=false;

      }else{
        return false

      }


    },

    // 选择移除学生改变时
    removeStudentsSelectionChange(val){

      this.RemoveMultipleSelection=val;

      if( val.length == 0){

        this.removeStudensState=true;

      }

      // console.log(this.RemoveMultipleSelection)
      
      if(this.RemoveMultipleSelection.length > 0){

        this.removeStudensState=false;

        this.saveStudensState=true;

      } 

      
      

      localStorage.setItem("removeStudents", JSON.stringify(this.RemoveMultipleSelection));

    },
    // 添加学生全选，反选
    addStudentsSelectionAll(){

      this.addStudensState= !!this.addStudensState;
      this.saveStudensState= !this.addStudensState;

    },
    // 移除学生全选，反选
    removeStudentsSelectionAll(){
      this.removeStudensState= !!this.removeStudensState;
      this.saveStudensState= !this.removeStudensState;
    },

    // 移除学生
    removeStudent(){


      let removeStudentsItem = JSON.parse(localStorage.getItem("removeStudents"));

      if(removeStudentsItem.length > 0 ){


        this.studentGradeListsData=_.concat(removeStudentsItem,this.studentGradeListsData);

        this.editStudentsListsData = _.differenceWith(this.editStudentsListsData , removeStudentsItem , _.isEqual );

        

        this.removeStudensState=true;
        this.saveStudensState=false;
        
        // this.getSutdentGradeLists();
        // this.saveStudents()

      }else{

        return false

      }

    },
    // 保存编辑学生数据
    saveStudents(){

      var sids= this.editStudentsListsData.map(item => item.sid).toString();

      // console.log(sids)

      let params={
          // classId: this.currentClassId,
          classId: 150,
          sids:sids
        };
        this.$ajax('/headteacher/saveUpgradeStudent',params)

        .then(res => {
          if(res.code == 200){

            this.$message({
              message: '保存成功',
              type: 'success'
            });

            this.dialogVisible = false;

            this.editStudentsListsData();

            localStorage.removeItem("addStudents");
            localStorage.removeItem("removeStudents");


          }

          

        }).catch(() => {

        });
    }

  }
};
</script>

<style >

.studentMgt-header{
  margin: 10px 0;

}
.studentMgt-filter .classes{
  margin: 10px 0;
}
.pagination{
  margin-top: 10px;
}
.studentMgt-body-title{
  display: flex;
}
.studentMgt-body-title .item:first-child{
  margin-right: 20px;
}
.studentMgt-body-table{
  margin-top: 10px;
}
.transfer-data {
  display:flex;
  margin-top: 10px;
}
.transfer-data .el-table__body-wrapper{
  height: 300px;
  overflow-x: hidden;
}
.transfer-data .transfer-data-left,
.transfer-data .transfer-data-right{
  width: 420px;
}
.transfer-data-header .title{
  margin: 10px 0;
}
.transfer-data-header .el-select{
  display: inline-block;
  width: 120px;
}
.transfer-data-header .el-input{
  margin-top: 10px;
}

.transfer-data-operation{
  width: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.transfer-data-operation .el-button{
  margin: 10px 0;
}
.transfer-data-body{
  margin-top: 10px;
}
.el-table__empty-block{
  overflow: hidden;
}
.el-table__empty-text {
  white-space: nowrap;
}
.el-button--primary a{
  color: #fff;
}
</style>



